.hd{
    display: flex;
    align-items: center;
    height: 1.78rem;
    background: url("../img/bg.png") center 0 #f37d0f;
    background-size: auto 100%;

    .avatar{
        height: 1.08rem;
        margin-left: .35rem;
        padding: 0.06rem 0.06rem;
        background-color: rgba(255,255,255,0.2);
        border-radius: 50%;
        img{
            width: 0.96rem;
            height: 0.96rem;
        }
    }
    .login{
        margin-left: .16rem;
        color: #ffffff;
        font-size: .27rem;
        font-weight: bold;
    }
}