.foot_one{
    width: 100vw;
    height: 1.04rem;
    position: relative;

    .foot_bot{
        display: flex;
        justify-content: space-between;
        padding: 0 0.2rem;
        width: 100%;
        height: 1.04rem;
        position: fixed;
        bottom: 0;
        left: 0;
        background: rgb(255, 255, 255);

        .footd{
            
            a{
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-content: space-around;
                color: #999;

                width: 1.5rem;
                height: 100%;
                text-align: center;
                i{
                
                    font-size: 0.46rem;  
                }  
                span{
                    font-size: 0.25rem;
                }
                em{
                    position: absolute;
                    top: 0;
                    font-size: 0.4rem;
                    color: rgb(254, 108, 2);
                }
                
            }
            // .active{
            //     color: rgb(254, 108, 2) !important;
            // }
            
        }
        
    }
}
